/**
 * @packageDocumentation
 * @module Serializer
 */
import { createSerializerOld } from '@ali/4ever-cangjie';
import schema from './strictSchema';
import type {
  MoHtmlParser,
  MoSerializationPlugin as Plugin,
} from '@ali/4ever-cangjie';
import type {
  Mo as IMo,
} from './type';

/**
 * Serializer，序列化工具
 */
function createSerializer(plugins: Plugin[], parser?: MoHtmlParser): IMo {
  return createSerializerOld(plugins, schema, parser);
}

export default createSerializer;
